  doQuery() {
    if (this.queryForm.invalid) {
      console.error("Please enter the query");

      return;
    }

    const rootLabel = (this.queryForm.value.db_query.match(
      /^.*?\:\`?([\w_-\d]+)[\`\s\{,\)]?/i
    ) || ["", popoto.graph.mainLabel])[1];
    const limit = (this.queryForm.value.db_query.match(/LIMIT\s(\d+)?/i) || [
      "",
      25
    ])[1];
    const statements = [
      {
        statement: this.queryForm.value.db_query,
        appQuery: true,
        parameters: { limit },
        resultDataContents: ["row"]
      }
    ];

    popoto.rest
      .post({
        statements: statements
      })
      .done(function(data) {
        const finaleRes = {
          label: rootLabel,
          rel: []
        };

        data.results[0].data.forEach(data => {
          if (data.meta[0] || data.meta[0].length) {
            const buffer: any = {};
            const meta = Array.isArray(data.meta[0]) ? data.meta[0] : data.meta;
            const row = Array.isArray(data.row[0]) ? data.row[0] : data.row;

            meta.forEach((metaItem, i) => {
              if (metaItem) {
                if (metaItem.type === "node") {
                  buffer.label = row[i].code;
                  buffer.value = Object.assign({}, row[i]);
                } else if (metaItem.type === "relationship") {
                  buffer.rel = {
                    label: row[i].code,
                    target: {
                      label: row[i + 1].code,
                      value: Object.assign({}, row[i + 1])
                    }
                  };
                }
              }
            });

            finaleRes.rel.push(buffer);
          }
        });

        popoto.graph.mainLabel = finaleRes;
        popoto.tools.reset();
      })
      .fail(function(xhr, textStatus, errorThrown) {
        console.error(xhr, textStatus, errorThrown);
      });
  }
