import { Injectable } from "@angular/core";
import { HttpHeaders, HttpClient } from "@angular/common/http";
import { Observable } from "rxjs";

const url: string = "http://54.236.44.131:32980/db/data/transaction/commit";

const httpOptions = {
  headers: new HttpHeaders({
    "Content-Type": "application/json",
    Authorization: "Basic bmVvNGo6bWFyYmxlcy1sZWFwcy1jb3Jyb3Npb24=",
    dataType: "json"
  })
};

@Injectable({
  providedIn: "root"
})
export class Neo4jService {
  constructor(private http: HttpClient) {}

  getData(query: any): Observable<any> {
    return this.http.post<any>(url, query, httpOptions);
  }
}
