import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { KeylinesComponent } from "./keylines/keylines.component";
import { PopotoComponent } from "./popoto/popoto.component";
import { AppComponent } from "./app.component";

const routes: Routes = [
  {
    path: "",
    component: AppComponent,
    children: [
      { path: '', redirectTo: '/keylines', pathMatch: 'full'},
      { path: "/keylines", component: KeylinesComponent },
      { path: "/popoto", component: PopotoComponent }
    ]
  },
  { path: "**", redirectTo: "keylines", pathMatch: "full" }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule {}
