import { Component, OnInit, AfterViewInit } from "@angular/core";
import {
  KlComponent,
  KlComponents,
  KlComponentsService
} from "../../../share/keylines/angular-keylines";

import { AngularNeo4jService } from "angular-neo4j";
import { Neo4jService } from "../services/neo4j.service";

const KeyLines: any = (<any>window).KeyLines;

const url = "bolt://localhost:7687";
const username = "neo4j";
const password = "demo";
const encrypted = true;

const colors: string[] = [
  "#8dd3c7",
  "#bebada",
  "#fb8072",
  "#80b1d3",
  "#fdb462",
  "#b3de69",
  "#fccde5",
  "#bc80bd"
];

@Component({
  selector: "app-keylines",
  templateUrl: "./keylines.component.html",
  styleUrls: ["./keylines.component.scss"]
})
export class KeylinesComponent implements OnInit, AfterViewInit {
  chart: KeyLines.Chart;

  neorequest;
  neoresponse;
  Neo4j;
  query: string;
  rawData;
  propertiesData = [];
  layoutKind: string = "";

  layoutNames: string[] = [
    "standard",
    "hierarchy",
    "sequential",
    "lens",
    "organic",
    "radial",
    "structural",
    "tweak"
  ];

  options = {
    logo: { u: "../../assets/logo.png", p: "se", x: 20, y: 20 },
    navigation: { shown: true },
    selfLinks: true
  };

  data: KeyLines.ChartData = {
    type: "LinkChart",
    items: []
  };

  setChartSize() {
    const w: number = window.innerWidth;
    const h: number = window.innerHeight;
    const border: number = 16;
    KeyLines.setSize("kl", w - border, h - border);
    this.chart.zoom("fit");
  }

  klReady([chart]) {
    this.chart = chart;
    chart.options(this.options);
    this.setChartSize();
  }

  constructor(
    private neo4j: AngularNeo4jService,
    private neo4jService: Neo4jService
  ) {
    this.Neo4j = () => {
      let api = {
        // Only the functions of this object will be accessible to the rest of the application.
        query: (cypherQuery, params, callback) => {
          let request = prepareRequest(cypherQuery, params);
          fetch(request, callback);
        }
      };
      let prepareRequest = (query, params) => {
        //  This will create an object that will be sent to the Neo4j api.
        return {
          statements: [
            {
              statement: query,
              parameters: params || {},
              resultDataContents: ["graph"]
            }
          ]
        };
      };
      let fetch = (request, callback) => {
        this.neorequest = JSON.stringify(request);

        this.neo4jService.getData(this.neorequest).subscribe(
          (response: any) => {
            this.rawData = response.results[0].data;
            console.log(this.rawData);
            callback(null, parseResponse(response));
          },
          (error: any) => {
            callback(
              "Unable to connect to Neo4j Server, check your password is correct.  If you are running Neo4j locally over https, please login to the console first at https://localhost:7473/."
            );
          }
        );
      };
      let parseResponse = response => {
        this.neoresponse = JSON.stringify(response);
        let items = [];
        // Get the data from the Neo response
        let data = response.results[0].data;
        for (let i = 0; i < data.length; i++) {
          let entryGraph = data[i].graph;
          // iterate through the list of nodes
          for (let j = 0; j < entryGraph.nodes.length; j++) {
            // Convert node to KeyLines format and add it to the items array.
            let node = makeNode(entryGraph.nodes[j]);

            this.propertiesData.push(entryGraph.nodes[j].properties);

            items.push(node);
          }
          // iterate through the list of links (also known as edges or relationships)
          for (let k = 0; k < entryGraph.relationships.length; k++) {
            // Convert link to KeyLines format and add it to the items array.
            let link = makeLink(entryGraph.relationships[k]);
            items.push(link);
          }
        }
        return items;
      };
      let makeNode = neoItem => {
        let data = neoItem.properties.name;
        let isTheEET = data === "EET";
        let color: string = colors[neoItem % colors.length];
        return {
          id: neoItem.id, // Unique id from neo.
          c: color, // Colour of the node.
          e: isTheEET ? 2 : 1, // Size of the node.
          type: "node", // Required, tells KeyLines this object is a node.
          t: data
        };
      };
      let makeLink = neoItem => {
        return {
          id: "link_" + neoItem.id, // Ensure link id can't clash with another node.
          type: "link", // Required, tells KeyLines this object is a link.
          id1: neoItem.startNode, // Node id link connects from.
          id2: neoItem.endNode // Node id link connects to.
        };
      };
      return api;
    };
  }

  ngOnInit() {}

  ngAfterViewInit() {
    this.getData();
  }

  updateLayer(name) {
    this.chart.layout(name);
  }

  getData(
    query: string = "MATCH p=()-->() RETURN p LIMIT 25"
  ) {
    var neo4j = this.Neo4j();
    var params = {};

    if (this.query !== "" && this.query !== undefined) {
      query = this.query;
    }

    neo4j.query(query, params, (error, items) => {
      this.data.items = items;
      console.log(items);
      this.chart.load(this.data);
      this.chart.layout();
    });
  }
}
